// src/lib/productLoader.js
export const getCategoryProducts = (slug) => {
  switch (slug) {
    case 'cervejas':
      return require('@/data/products_cervejas.json').default;
    case 'refrigerantes':
      return require('@/data/products_refrigerantes.json').default;
    case 'agua':
      return require('@/data/products_agua.json').default;
    case 'destilados':
      return require('@/data/products_destilados.json').default;
    case 'energeticos':
      return require('@/data/products_energeticos.json').default;
    case 'outros':
      return require('@/data/products_outros.json').default;
    default:
      return [];
  }
};
